
@import "mixin";



body {
	width: 100%;
	min-width: 320px;
	margin: 0 auto;
	background: #f9f9fb; }



.top {
	margin: auto;
	height: 345px;
	background-color: $grey;
	text-align: center; }


.navtop {
	max-width: $widthmax;
	width: $widthall;
	display: inline-block;
	margin: auto;
	// display: -webkit-flex
	// display: -moz-flex
	// display: -ms-flex
	// display: -o-flex
	// display: flex
	// justify-content: space-between
	position: relative;
	text-align: right; }

.ninja {
	width: 60px;
	height: 55px;
	display: inline-block;
	border-left: 1px solid #3c3c3c;
	border-right: 1px solid #3c3c3c;
	background: url('../image/menu.png') center center no-repeat;
	position: relative;
	z-index: 100;
	float: left; }
.ninja:hover {
	@include box-shadow(0px 0px 10px 4px rgba(112,112,112,.49)); }


.head-ul {
	display: none;
	background: $grey;
	padding: 0;
	margin: 0;
	position: absolute;
	top: 55px;
	text-align: center;
	z-index: 101;
	font-family: "Hind";
	letter-spacing: 3px; }

.ninja:focus ~ .head-ul,
.ninja:active ~ .head-ul {
	@include transition(.2s);
	display: block; }
.head-li {
	list-style-type: none;
	width: 180px; }
.item {
	text-decoration: none;
	color: $white;
	display: block;
	padding: 20px 0px;
	@include transition(.2s); }

.item:hover {
	@include transition(.2s);
	background: darken($grey, 20%); }

.logo {
	text-align: center;
	line-height: 55px;
	position: absolute;
	top: 0px;
	left: 0;
	right: 0;
	margin: auto;
	z-index: 0; }

.socnav {
	text-align: center;
	width: 60px;
	border-left: 1px solid #3c3c3c;
	display: inline-block;
	// float: right
	position: relative;
	// right: -25%
	font-size: 16px;
	line-height: 55px;
	text-decoration: none;
	color: $white;
	position: relative;
	z-index: 100;
	@include transition(.2s); }

.socnav:last-child {
	border-right: 1px solid #3c3c3c; }
.socnav:hover {
	color: darken($white, 70%); }

.baner {
	max-width: $widthmax;
	width: $widthall;
	height: 580px;
	background: url('../image/1.png') center no-repeat;
	background-size: cover;
	margin: auto;
	position: relative; }
.text {
	max-width: 500px;
	min-width: 300px;
	color: $white;
	font-family: "Hind";
	letter-spacing: 3px;
	font-size: 18px;
	font-weight: bold;
	text-align: left;
	margin-left: 7%;
	position: relative;
	top: 50%;
	transform: translateY(-50%); }
.capt {
	position: relative;
	top: 10px;
	font-size: 40px;
	text-transform: uppercase;
	letter-spacing: 6px;
	line-height: 55px; }


.newsletter {
	max-width: $widthmax;
	width: $widthall;
	padding: 20px 0	20px;
	margin: auto;
	background: $wsg;
	border: 1px solid $wg;
	@include box-shadow(0 0 0 1px rgba(232,232,232,.11)); }

.singup {
	color: $grey;
	font-family: "Montserrat";
	font-size: 20px;
	font-weight: bold;
	text-transform: uppercase;
	margin-left: 30px;
	letter-spacing: 2px;
	text-align: left;
	float: left; }

.form {
	width: 65%; }
.singup, .name, .mail, .sub, .form {
	display: inline-block; }

.name, .mail {
	width: 40%;
	margin-right: 5%;
	text-align: center; }

.sub {
	width: 15%;
	min-width: 165px;
	margin-right: 30px;
	float: right; }

input[type="login"], input[type="mail"], input[type="submit"] {
	width: 100%;
	height: 50px;
	border: none;
	letter-spacing: 1px;
	padding-left: 30px;
	font-family: "Hind";
	font-size: 18px;
	color: $grey; }
input[type="login"], input[type="mail"] {
	// min-width: 220px
	// position: relative
 }	// left: -30px

input[type="submit"] {
	color: $grey;
	border: none;
	background: $grey;
	color: $white;
	font-family: "Hind";
	font-size: 14px;
	padding-left: 0px;
	padding-right: 0px;
	letter-spacing: 3px;
	cursor: pointer;
	@include transition(.3s); }

input[type="submit"]:hover {
	background: lighten($grey,20%); }


.feed {
	max-width: $widthmax;
	width: $widthall;
	margin: 390px auto 60px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	flex-wrap: wrap;
	.news-column:first-child {
		article:last-child {
			background: url(../image/Layer.png) no-repeat;   //0.1
			background-size: cover;
			padding-top: 20px; }
		.arttext {
			min-height: 175px; } }
	.news-column:nth-child(2) {
		.newsfeed:nth-child(3) {
			video {
				width: 100%;
				height: 235px; }
			.write {
				position: relative;
				top: -235px; }
			.arth1:nth-child(3) {
				margin-top: 1px; } }

		.newsfeed:nth-child(2):hover {
			@include box-shadow(0 3px 26px 7px rgba(112,112,112,.49)); } }

	.news-column:nth-child(3) {
		.newsfeed:nth-child(2) {
			.newspic {
				height: 257px; } }
		.newsfeed:nth-child(3) {
  		.arttext {
  			word-wrap: break-word;
  			position: relative;
  			top: -15px; }
				a {
  				color: $white;
  				@include transition(.2s); }
				a:hover {
  				color: darken($white, 30%); } }

		.newsfeed:last-child {
			height: 314px; } } }

.news-column {
	margin-top: 75px;
	min-width: 320px;
	width: 23%; }


.newsfeed {
	background-color: #fff;
	margin-bottom: 30px;
	@include transition(.2s);
	border: 1px solid $postborder;
	@include box-shadow(0 0 0 (232,232,232,.11)); }

.nc {
	height: 511px;
	background-color: #fff; }

.px {
	margin-top: 45px; }

.dark {
	background: $grey;
	.arth1 {
		margin-bottom: 40px; }
	.arttext {
		color: $white;
		letter-spacing: 2px; }
	.write {
		background: $white;
		i {
			color: $grey; } }
	.write:hover {
			@include box-shadow(0px 0px 10px 4px rgba(256,256,256,.9)); } }

.newsfeed:hover {
	@include box-shadow(0 3px 26px 3px rgba(112,112,112,.49)); }

.write {
	width: 40px;
	line-height: 35px;
	background-color: $grey;
	color: #fff;
	text-align: center;
	margin: auto;
	transform: translateY(-50%);
	@include transition(.2s); }
.write:hover {
	@include box-shadow(0px 0px 10px 4px rgba(112,112,112,.49)); }

.arth1 {
	text-transform: uppercase;
	color: $arth1;
	font-size: 16px; //80%
	font-family: "Hind";
	display: block;
	text-align: center;
	margin: 20px 0 0 0; }

.arth2 {
	color: $grey;
	font-size: 20px; //100%
	font-weight: bold;
	font-family: "Montserrat";
	margin: 15px 0 0px 0; }

.arttext {
	max-height: 220px;
	overflow: hidden;
	color: $arttext;
	font-size: 15px;
	font-family: "Hind";
	text-align: center;
	letter-spacing: 1px;
	margin: 30px 0 30px 0;
	padding: 0 30px; }//5%

.readmore {
	margin: auto;
	width: 100px;
	display: block;
	text-align: center;
	border-left: 1px solid $rborder;
	border-right: 1px solid $rborder;
	margin-top: 40px;
	margin-bottom: 40px;
	color: $grey;
	text-decoration: none;
	font-size: 14px; //80%
	font-family: "Hind";
	font-weight: bold; }

.readmore:hover {
	text-decoration: underline; }

.comment {
	border-top: 1px solid $wsg;
	padding: 20px 30px;
	font-family: Hind;
	font-size: 14px;
	line-height: 25px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	a {
		text-decoration: none;
		color: $arttext;
		@include transition(.1s);

		span {
			margin-left: 10px; } }
	a:hover {
		font-size: 15px; } }

.soc {
	a {
		margin-left: 10px;
		i {
			width: 20px; } } }

.newspic {
	width: 100%;
	height: 270px;
	background: $npgrey;
	font-size: 0;
	letter-spacing: 0; }

.galery {
	width: 50%;
	height: 50%;
	background: $white;
	display: inline-block;
	column-count: 2;
	margin: 0;
	border-spacing: 0;
	@include box-shadow(0 0 0 1px rgba(232,232,232,0.5));
	@include transition(.2s); }

.galery:hover {
	background: $npgrey; }

.circle {
	width: 135px;
	height: 135px;
	margin: 30px auto 143px;
	@include border-radius(67px);
	background: #999;
	@include transition(.2s); }

.circle:hover {
	@include box-shadow(0px 0px 10px 4px rgba(112,112,112,.49)); }

footer {
	background: $grey; }

.foot {
	max-width: $widthmax;
	width: $widthall;
	margin: auto;
	padding-bottom: 30px;
	color: $white;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	.foot-column:nth-child(2) {
		a {
			text-decoration: none;
			color: darken($white, 5%);
			font-size: 15px;
			font-family: "Hind";
			letter-spacing: 1px;
			@include transition(.2s); }
		a:hover {
			color: darken($white, 50%); }
		.footer-text {
			padding-left: 22px;
			margin-bottom: 40px;
			line-height: 25px; } }
	.foot-column:nth-child(3) {
		.galery {
			max-width: 107px;
			max-height: 100px;
			column-count: 2;
			@include box-shadow(0 0 0 1px rgba(232,232,232,0.5)); }
		.galery:hover {
			background: darken($npgrey,20%); } }

	.foot-column:nth-child(4) {
		.foot-comenter {
			margin-bottom: 0px; }
		.footer-text:nth-child(3) {
			padding-bottom: 30px;
			margin-bottom: 30px;
			border-bottom: 1px solid darken($white, 20%); } } }

.foot-column {
	min-width: 320px;
	width: 24.5%;
	margin: 0 0px 0; }

.foot-h {
	color: darken($white, 5%);
	text-transform: uppercase;
	margin: 60px 0 40px;
	font-weight: 400;
	letter-spacing: 1px; }

.footer-text {
	color: darken($white, 5%);
	font-size: 15px;
	font-family: "Hind";
	letter-spacing: 1px;
	padding-right: 5px; }

.foot-capt {
	height: 145px;
	background: $footblock;
	margin-top: 10px;
	img {
		position: relative;
		top: 100%;
		left: 100%;
		transform: translate(-100%, -100%);
		margin-top: -10px;
		margin-left: -10px; } }
.arr {
	background: darken($footblock, 5%);
	color: darken($white, 50%);
	text-transform: uppercase;
	text-align: center;
	line-height: 40px;
	font-family: MyriadPro;
	letter-spacing: 1px;
	div {
		display: inline-block; }
	a {
		color: darken($white, 50%);
		text-decoration: none;
		border-left: 1px solid;
		padding-left: 15px;
		margin-left: 15px;
		span {
			@include transition(.2s); }
		span:hover {
			color: darken($white, 10%); } } }

@import "screen";
